import React from 'react';
import { Link } from 'react-router-dom';

import Main from '../layouts/Main';

import ExperienceComponent from '../components/Resume/Experience';

import work from '../data/work';

// NOTE: sections are displayed in order defined.
const sections = {
  Experience: () => <ExperienceComponent data={work} />,
};

const Experience = () => (
  <Main
    title="Experience"
    description="Jake Martins's Experince. NextGen.net, nextgen.net, UNSW, UNSW Climate Change Research Center, Climate Change Research Center, Climate Change, Machine Learning."
  >
    <article className="post" id="experience">
      <header>
        <div className="title">
          <h2><Link to="/experience">Experience</Link></h2>
        </div>
      </header>
      {Object.entries(sections).map(([name, Section]) => (
        <Section key={name} />
      ))}
    </article>
  </Main>
);

export default Experience;
